.modal-container__form-item {
  position: relative; }
  .modal-container__form-item:before {
    content: '';
    position: absolute;
    top: -5px;
    left: 10px;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    color: #777777;
    background: #ffffff;
    padding-left: 2px;
    padding-right: 2px;
    opacity: 0;
    transition: 0.2s; }
  .modal-container__form-item.active:before {
    opacity: 1; }
  .modal-container__form-item input::-webkit-input-placeholder {
    transition: 0.2s; }
  .modal-container__form-item input::-moz-placeholder {
    transition: 0.2s; }
  .modal-container__form-item input:-moz-placeholder {
    transition: 0.2s; }
  .modal-container__form-item input:-ms-input-placeholder {
    transition: 0.2s; }
  .modal-container__form-item input:focus::-webkit-input-placeholder {
    opacity: 0; }
  .modal-container__form-item input:focus::-moz-placeholder {
    opacity: 0; }
  .modal-container__form-item input:focus:-moz-placeholder {
    opacity: 0; }
  .modal-container__form-item input:focus:-ms-input-placeholder {
    opacity: 0; }

